.nav-opened {
  position: absolute;
  z-index: 1;
  top: 0;
  right: -300px;
  display: block;
  width: 1400px;
  height: 12000px;
  background-color: @basic_dark;

  @media (max-width: @mobile-width) {
    position: absolute;
    // z-index: 1;
    top: 0;
    right: -220px;
    display: block;
    width: 800px;
    height: 10000px;
    background-color: @basic_dark;
  }
}

.nav-item-head {
  display: none;

  @media (max-width: @tablet-width) {
    display: flex;  
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    margin-bottom: 85px;
  }

  @media (max-width: @mobile-width) {
    position: relative;
    margin-bottom: 30px;
    width: 328px;
    height: 100px;
  }
}

.nav-img-head {


  @media (max-width: @tablet-width) {
    display: block;
    width: 70px;
    height: 70px;
    margin-left: 783px;
    margin-top: 15px;
  }

  @media (max-width: @mobile-width) {
    margin-left: 15px;
    margin-top: 18px;
  }
}
.nav-img-head-logo {
  width: 70px;
  height: 70px;

  @media (max-width: @mobile-width) {
    width: 50px;
    height: 50px;
  }
}
.nav-button-contact-head {

  @media (max-width: @tablet-width) {
    display: block;
    margin-right: 366px;
    margin-top: 15px;
  }

  @media (max-width: @mobile-width) {
    margin-right: 50px;
    margin-top: 0;
    width: 36px;
    height: 36px;
  }
}
.nav__toggle-head {
  display: none;


  @media (max-width: @tablet-width) {
    display: block;
    content: "";
    position: absolute;
    z-index: 5;
    top: 50px;
    right: 303px;
    width: 30px;
    height: 2px;
    background-color: #FDFDFD;
    transform: rotate(45deg);

    &::before {
      content: "";
      position: absolute;
      z-index: 4;
      top: 0;
      right: 0;
      width: 30px;
      height: 2px;
      background-color: #FDFDFD;
      transform: rotate(-90deg);
    }
  }

  @media (max-width: @mobile-width) {
    display: block;
    position: absolute;
    z-index: 5;
    top: 45px;
    right: -2px;
    background-color: #303030;
    width: 30px;
    height: 30px;

    &::after {
    content: "";
    position: absolute;
    z-index: 5;
    top: 0;
    right: 0;
    width: 30px;
    height: 2px;
    background-color: #FDFDFD;
    }

    &::before {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      width: 30px;
      height: 2px;
      background-color: #FDFDFD;
    }
  }
}
.nav-item-social {
  display: none;

  @media (max-width: @tablet-width) {
    display: block;
    width: 313px;
    height: 74px;
    margin-left: 783px;
    color: @background_page;
    font-family: "Satoshi-Bold", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 140%;
    text-transform: uppercase;
    text-align: right;
    margin-top: 150px;

    &::after {
      content: "";
      position: absolute;
      top: 712px;
      right: 0;
      width: 100%;
      height: 1px;
      background-color: #A0A0A0;
    }
  }

  @media (max-width: @mobile-width) {
    display: none;
  }
}
.nav-item-row1 {
  display: flex;
  justify-content: space-between;
}
.nav-item-row2 {
  width: 200px;
  margin-left: 113px;
  margin-top: 33px;
  display: flex;
  justify-content: space-between;
}

